import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { ProfileService } from '../profile/profile.service';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { ActivatedRoute } from '@angular/router';
import { Access } from 'core/providers/access.service';
import { MFA } from 'config/constants/user';
import { ExaTask } from '../../../core/providers/exatask.service';

class ChangePassword {

  current_password: string = '';
  new_password: string = '';
  confirm_password: string = '';
}

const error_map = {
};

@Component({
  selector: 'app-security',
  templateUrl: 'security.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [ProfileService]
})
export class SecurityComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  password: ChangePassword;
  returnUrl: string;

  error: string;
  invalid_attributes: Array<string>;

  qrCode: boolean = false;
  questionAnswer: boolean = false;
  securityKey: boolean = false;

  mfaAllowed: boolean = false;

  constructor(
    private profileService: ProfileService,
    private route: ActivatedRoute,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.exatask.setTitle('Security');
    this.password = new ChangePassword();

    this.error = '';
    this.invalid_attributes = [];
  }

  async ngOnInit() {

    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/dashboard';

    this.mfaAllowed = await this.access.hasAccess({
      module: 'user',
      entity: 'mfa',
      action: 'enable'
    });

    if (this.mfaAllowed) {

      this.__loadMfa();
    }
  }

  /**
   * @todo Implement any and all necessary validations
   */
  private __validate() {
    return true;
  }

  private __loadMfa() {

    this.profileService.getProfile('auth').then(data => {

      if (data.mfa) {

        for (const mfa of data.mfa) {

          switch (mfa.mfa_type) {

            case MFA.QR_CODE:
              this.qrCode = true;
              break;

            case MFA.QUESTION_ANSWER:
              this.questionAnswer = true;
              break;
          }
        }
      }

    }).catch(err => {

      this.error = err;
      this.errorModal.show();
    });
  }

  public changePassword() {

    if (this.__validate()) {

      const change_password = {
        current_password: this.password.current_password,
        new_password: this.password.new_password
      };

      this.profileService.changePassword(change_password).then(data => {

        this.successModal.show(this.returnUrl);
        sessionStorage.removeItem('restrict');
        sessionStorage.removeItem('url');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }
}
