import { Component, OnInit, ViewChild } from '@angular/core';
import { clone } from 'lodash';

import { SOCIAL_SECURITY_TYPE } from 'config/constants/user';
import {
  Profile, ContactNumber, EmergencyContact, SocialSecurity, ProfileService, DateOfBirth, Name
} from 'modules/user/profile/profile.service';
import Utility from 'shared/helpers/utility';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { Address } from '../../../shared/components/address/address.component';
import { ExaTask } from '../../../core/providers/exatask.service';

class EmailId {
  email_id: string;
}

const error_map = {
  first: 'First Name',
  middle: 'Middle Name',
  last: 'Last Name',
  locality: 'Locality (Address)',
  contact_number: 'Contact Number',
  name: 'Name'
};

@Component({
  selector: 'app-profile',
  templateUrl: 'profile.component.html',
  providers: [ProfileService]
})
export class ProfileComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  profile: Profile;
  original_profile: Profile;

  alternate_email_ids: Array<EmailId>;

  maxSocialSecurityTypes: Boolean;

  error: string;
  invalid_attributes: Array<string>;
  dobConfig: Object;

  constructor(
    private profileService: ProfileService,
    public exatask: ExaTask
  ) {

    this.exatask.setTitle('Profile');
    this.profile = new Profile();

    this.maxSocialSecurityTypes = false;
    this.dobConfig = {
      maxDate: new Date(),
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false
    };

    this.error = '';
    this.invalid_attributes = [];
  }

  /**
   * @todo Implement validation for all profile fields
   */
  private __validate() {
    return true;
  }

  private __sanitizeUpdate(profile) {

    if (profile.permanent_address && profile.permanent_address.postcode) {
      profile.permanent_address = this.profile.permanent_address;
    } else {
      profile.permanent_address = undefined;
    }

    if (profile.correspondence_address && profile.correspondence_address.postcode) {
      profile.correspondence_address = this.profile.correspondence_address;
    } else {
      profile.correspondence_address = undefined;
    }

    if (profile.date_of_birth) {

      const date_of_birth = new Date(profile.date_of_birth.toString());
      profile.date_of_birth = {
        day: date_of_birth.getDate(),
        month: date_of_birth.getMonth() + 1,
        year: date_of_birth.getFullYear()
      };

    } else {
      profile.date_of_birth = undefined;
    }

    return profile;
  }

  ngOnInit() {

    this.profileService.getProfile('profile1,profile2,profile3')
      .then(profile => {

        this.profile = clone(profile);
        this.original_profile = clone(this.profile);

        if (!this.profile.alternate_email_ids) {
          this.profile.alternate_email_ids = new Array();
        }

        if (!this.profile.alternate_contact_numbers) {
          this.profile.alternate_contact_numbers = new Array();
        }

        if (!this.profile.social_security) {
          this.profile.social_security = new Array();
        }

        if (!this.profile.emergency_contacts) {
          this.profile.emergency_contacts = new Array();
        }

        if (!this.profile.permanent_address) {
          this.profile.permanent_address = new Address();
        }

        if (!this.profile.correspondence_address) {
          this.profile.correspondence_address = new Address();
        }

        if (this.profile.date_of_birth) {

          const date_of_birth = <DateOfBirth> this.profile.date_of_birth;
          this.profile.date_of_birth = date_of_birth.year + '-' + date_of_birth.month + '-' + date_of_birth.day;
          this.original_profile.date_of_birth = this.profile.date_of_birth;
        }

        this.maxSocialSecurityTypes = (this.profile.social_security.length === Object.keys(SOCIAL_SECURITY_TYPE).length);

        this.alternate_email_ids = new Array();
        for (const i of this.profile.alternate_email_ids) {
          const emailId = new EmailId();
          emailId.email_id = i;
          this.alternate_email_ids.push(emailId);
        }

        this.exatask.setTitle('Profile : ' + Name.getFullName(this.profile.name));
      });
  }

  public addEmailId() {

    const emailId = new EmailId();
    emailId.email_id = '';
    this.alternate_email_ids.push(emailId);
  }

  public removeEmailId(index) {
    this.alternate_email_ids.splice(index, 1);
  }

  public addContactNumber() {

    const contactNumber = new ContactNumber();
    contactNumber.isd_code = '+91';
    contactNumber.contact_number = '';
    this.profile.alternate_contact_numbers.push(contactNumber);
  }

  public removeContactNumber(index) {
      this.profile.alternate_contact_numbers.splice(index, 1);
  }

  public addSocialSecurity() {

    const socialSecurity = new SocialSecurity();
    socialSecurity.type = '';
    socialSecurity.number = '';
    this.profile.social_security.push(socialSecurity);
    this.maxSocialSecurityTypes = this.profile.social_security.length === Object.keys(SOCIAL_SECURITY_TYPE).length;
  }

  public removeSocialSecurity(index) {

    this.profile.social_security.splice(index, 1);
    this.maxSocialSecurityTypes = this.profile.social_security.length === Object.keys(SOCIAL_SECURITY_TYPE).length;
  }

  public addEmergencyContact() {

    const emergencyContact = new EmergencyContact();
    emergencyContact.name = '';
    emergencyContact.relation = '';
    emergencyContact.isd_code = '+91';
    emergencyContact.contact_number = '';
    this.profile.emergency_contacts.push(emergencyContact);
  }

  public removeEmergencyContact(index) {
    this.profile.emergency_contacts.splice(index, 1);
  }

  public updateProfile() {

    this.profile.alternate_email_ids = new Array();
    for (const i of this.alternate_email_ids) {
      if (i.email_id.length > 0) {
        this.profile.alternate_email_ids.push(i.email_id);
      }
    }
    this.profile.alternate_email_ids = Array.from(new Set(this.profile.alternate_email_ids));

    const social_security = {};
    for (const social of this.profile.social_security) {
      if (social.type.length > 0 && social.number.length > 0) {
        social.verified = false;
        social_security[social.type] = social;
      }
    }

    this.profile.social_security = new Array();
    for (const key in social_security) {
      if (social_security.hasOwnProperty(key)) {
        this.profile.social_security.push(social_security[key]);
      }
    }

    const emergency_contacts = new Array();
    for (const contact of this.profile.emergency_contacts) {
      if (contact.name.length > 0 && contact.relation.length > 0 && contact.contact_number.length > 0) {
        emergency_contacts.push(contact);
      }
    }
    this.profile.emergency_contacts = Array.from(new Set(emergency_contacts));

    if (this.__validate()) {

      let profile = Utility.compare(this.original_profile, this.profile);
      if (profile !== undefined) {

        profile = this.__sanitizeUpdate(profile);
        this.error = '';
        this.invalid_attributes = [];

        this.profileService.updateProfile(profile).then(data => {

            this.original_profile = clone(this.profile);
            this.successModal.show('/dashboard');

          }).catch(err => {

            this.error = err.error.message.text;
            if (err.error.invalid_attributes) {
              for (const key in err.error.invalid_attributes) {
                if (err.error.invalid_attributes.hasOwnProperty(key)) {
                  this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
                }
              }
            }
            this.errorModal.show();
          });
      }
    }
  }
}
